<template>
  <div class="contact-us">
    <Banner :data="banner" />
    <b-container>
      <Breadcrumbs :items="items" class="px-0" />
    </b-container>
    <ContactUs />
    <VisitUs />
  </div>
</template>

<script>
import Banner from '@/components/shared/Banner'
import Breadcrumbs from '@/components/shared/Breadcrumbs'
import ContactUs from '@/components/contact-us/ContactUs'
import VisitUs from '@/components/contact-us/VisitUs'

export default {
  name: 'contact-us',
  components: {
    Banner,
    Breadcrumbs,
    ContactUs,
    VisitUs,
  },
  data() {
    return {
      items: [
        {
          text: this.$t('breadcrumb.home'),
          to: this.localePath('/'),
        },
        {
          text: this.$t('contactUs.contactUs'),
          to: this.localePath('/contact-us'),
        },
      ],
      banner: {
        title: this.$t('contactUs.contactUs'),
      },
    }
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Contact US" : "إتصل بنا"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? "Contact US" : "إتصل بنا",
        }
      ]
    }
  },
  computed: {
    appEnv() {
      return this.$config.APP_ENV
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
  }
}

</script>

<style lang="scss" scoped>
.contact-us {
  background-color: $grayBackgroundColor;
}
</style>
