<template>
  <div class="form">
    <h2>{{ $t('contactUs.emailUsOn') }}</h2>

    <b-overlay :show="sendingMessage" v-if="!messageSent">
      <b-form @submit.prevent="sendData">
        <div class="mb-4" v-if="options && options.length">
          <v-select
            :placeholder="$t('contactUs.pleaseSelectRequestType')"
            v-model="request"
            :options="options"
            required
          >
            <template #search="{ attributes, events }">
              <input
                class="vs__search"
                :required="!request"
                v-bind="attributes"
                v-on="events"
              />
            </template>
          </v-select>
        </div>
        <div class="mb-4">
          <AppInput
            :validation-name="$t('contactUs.emailPhone')"
            validation-rules="required|phoneOrEmail"
            :placeholder="$t('contactUs.emailPhone')"
            v-model="emailPhone"
            required
            class="mb-0"
          ></AppInput>
        </div>
        <div class="mb-4">
          <b-form-textarea
            id="property-description"
            v-model="message"
            placeholder="Enter your message"
            required
            :state="messageState"
            rows="3"
          >
          </b-form-textarea>
          <b-form-invalid-feedback id="input-live-feedback">
            Enter at least 10 letters
          </b-form-invalid-feedback>
        </div>
        <b-button variant="primary" type="submit" :disabled="!this.messageState"
          >{{ $t('contactUs.sendEmail') }} →
        </b-button>
      </b-form>
    </b-overlay>
    <span v-if="messageSent" class="text-success font-weight-bold">{{
      $t('contactUs.weRecievedYourMessage')
    }}</span>
  </div>
</template>

<script>
export default {
  name: 'ContactForm',
  data() {
    return {
      request: null,
      emailPhone: null,
      message: '',
      options: [
        this.$t('contactUs.inquiry'),
        this.$t('contactUs.complaint'),
        this.$t('contactUs.others'),
      ],
      messageSent: false,
      sendingMessage: false,
    }
  },
  computed: {
    messageState() {
      if (this.message.length === 0) return
      return this.message.length > 9 || false
    },
  },
  methods: {
    sendData() {
      this.sendingMessage = true
      if (!this.request || !this.emailPhone || !this.message) return
      this.$axios
        .$post('/sendContactUs', {
          subject: this.request,
          contact_address: this.emailPhone,
          message: this.message,
        })
        .then((res) => {
          this.messageSent = true
          this.request = null
          this.emailPhone = null
          this.message = ''
        })
        .catch((err) => {})
        .finally(() => {
          this.sendingMessage = false
          setTimeout(() => {
            this.messageSent = false
          }, 3000)
        })
    },
  },
}
</script>

<style lang="scss" scoped>
.form {
  background: #fff;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 15px;
}
</style>
